import { FirestoreProvider } from './../../providers/firestore/firestore';
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, Loading, LoadingController, AlertController } from 'ionic-angular';
import { OtuLoggerProvider } from '../../providers/otu-logger/otu-logger';
import { OtuConfigProvider } from '../../providers/otu-config/otu-config';

/**
 * Generated class for the UserItineraryPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-user-itinerary',
  templateUrl: 'user-itinerary.html',
})
export class UserItineraryPage {

  itrCode;
  itinerary$;
  lang$;
  lang = 'en';
  itrList;
  token;
  errMessage;

  public loading: Loading;

  constructor(
      public navCtrl: NavController
    , public navParams: NavParams
    , private firestore: FirestoreProvider
    , private logger: OtuLoggerProvider
    , private config: OtuConfigProvider
    , public loadingCtrl: LoadingController
    , public alertCtrl: AlertController
  ) {
    alert('Itr.ts');
    this.lang = 'en';
    this.loading = this.loadingCtrl.create();
    this.loading.present();
    this.itrCode = this.firestore.getItrCode();
    alert('Itt.ts  - ITR Code is ' + this.itrCode);
    if (this.itrCode == undefined || this.itrCode == '') {
      let loopCount = 0;

      let interval = setInterval(() => {
        loopCount++;
        this.token = this.logger.getToken();
        if (this.token != undefined && this.token != null && this.token != '' && loopCount < 30) {
          alert('Itt.ts  - Token is ' + this.token);
          clearInterval(interval);
          this.firestore.getLastUsedItrCode(this.token).valueChanges().subscribe((c) => {
            if (c != undefined && c.length > 0) {
              this.itrCode = c[0]['itrCode'];
              this.getItinerary();
            } else {
              this.loading.dismiss();
            }
          })
        } else {
          if (loopCount >= 30) {
            alert('Itt.ts  - No token received ');
            clearInterval(interval);
            this.loading.dismiss();
          }
        }
      }, 100);
    } else {
      this.getItinerary();
    }
    this.config.getLanguage().subscribe((l) => this.lang = l);
    this.firestore.getOtherPackages().valueChanges().subscribe((p) => {
      this.itrList = p;
    });
  }

  ionViewDidLoad() {
    // console.log('ionViewDidLoad UserItineraryPage');
  }
  // ionViewWillLeave() {
  //   if(this.token != undefined) {
  //     this.firestore.rememberItrCode(this.token, this.itrCode);
  //   }
  // }

  getItinerary() {
    alert('Itt.ts  - Get Itr with ' + this.itrCode);
    // alert(`Token Id: ${this.token} & Itr Code: ${this.itrCode}`)
    this.logger.eventLog('getItinerary', { code: this.itrCode });
    
      let interval = setInterval(() => {
        this.token = this.logger.getToken();
        if (this.token != undefined) {
          clearInterval(interval);
          alert(`Saving ITR Code with token: ${this.token} & code: ${this.itrCode}`);
          this.firestore.rememberItrCode(this.token, this.itrCode);
          this.itinerary$ = this.firestore.getItinerary(this.itrCode.trim()).valueChanges();
          this.itinerary$.subscribe(i => {
            // console.log(i);
            this.loading.dismiss();
          }, e => {
            this.errMessage = e;
            this.loading.dismiss();
          });
        }
      }, 100)
  }
  changeLanguage() {
    let from = this.lang;
    if (this.lang == "en") {
      this.lang = "kn";
    } else {
      this.lang = "en";
    }
    this.config.setLanguage(this.lang);
    this.logger.eventLog('changeLanguage', { from: from, to: this.lang });
  }
}



